export const SET_APP_THEME = 'SET_APP_THEME';
export const setApplicationTheme = theme => ({
  type: SET_APP_THEME,
  theme
});

export const SET_BOARD_THEME = 'SET_BOARD_THEME';
export const setBoardTheme = theme => ({
  type: SET_BOARD_THEME,
  theme
});



